<template>
  <div class="wrapper">
    <v-header v-on:getMenu="getMenu"></v-header>
    <v-sidebar ref="sidebar"></v-sidebar>
    <div class="content" :class="this.$store.state.isCollapse?'bigcontent':'smallcontent'">
      <v-tags></v-tags>
      <el-scrollbar style='height:100%;overflow-x: hidden'>
        <transition name="move" mode="out-in">
          <!-- 增加keep-alive缓存机制 -->
          <keep-alive :include="tagsList">
            <router-view></router-view>
          </keep-alive>
        </transition>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>
  import vHeader from './header.vue'
  import vSidebar from './sidebar.vue'
  import VTags from './tags.vue'
  import bus from '@/utils/bus'
  export default {
    components: {
      vHeader,
      vSidebar,
      VTags,
    },
    created() {
      bus.$on('collapse-content', msg => {
        console.log(msg)
      });
      // 用于keep-alive缓存，只有在标签页列表里的页面才使用keep-alive，即关闭标签之后就不保存到内存中了。
      bus.$on('tags', msg => {
        let arr = []
        for (let i = 0, len = msg.length; i < len; i++) {
          msg[i].name && arr.push(msg[i].name)
        }
        this.tagsList = arr
      })
    },
    data() {
      return {
        tagsList: [],
        collapse: false
      }
    },
    mounted() {

    },
    methods: {
      reFreshMenu() {
        // this.$refs.sidebar.getMenu()
      },
      getMenu() {
        // this.reFreshMenu()
      }
    }
  }
</script>
<style scoped>
  .bigcontent {
    left: 64px;
  }

  .smallcontent {
    left: 200px;
  }

  .wrapper .content {
    position: absolute;
    top: 42px;
    bottom: 0;
    /*left: 200px;*/
    right: 0px;
    width: auto;
    overflow: hidden;
    background: #F2F2F2;
  }

  .wrapper .el-select-dropdown .el-popper {
    top: 80px;
  }

  .content .el-scrollbar__wrap {
    overflow-x: hidden;
  }
</style>



