<template>
  <div class="sidebar" :class="this.$store.state.isCollapse?'sidebarhidden':'sidebarshow'">
    <el-menu :collapse="this.$store.state.isCollapse" :default-active="activeRouter" class="el-menu-vertical-demo"
      router :unique-opened="true" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
      <el-scrollbar style='height:100%'>
        <template v-for="item in items">
          <template v-if="item.childMenus">
            <el-submenu :index="item.router">
              <template slot="title">
                <i :class="item.icon"></i>
                <span style="font-size:16px;margin-left:12px;">{{ item.name }}</span>
              </template>
              <el-menu-item v-for="(subItem,i) in item.childMenus" :key="i" :index="subItem.router">
                {{ subItem.name }}
              </el-menu-item>
            </el-submenu>
          </template>
          <template v-else>
            <el-menu-item :index="item.router">
              <i :class="item.icon"></i>
              <span style="margin-left:12px;">{{ item.name }}</span>
            </el-menu-item>
          </template>
        </template>
      </el-scrollbar>
    </el-menu>

  </div>
</template>

<script>
  import {
    surpermission
  } from '@/utils/public'
  export default {
    data() {
      return {
        surpermissionFlag: surpermission(),
        isCollapse: this.$store.state.isCollapse,
        activeRouter: this.$route.path.replace(/\//g, ''),
        items: [

        ],
        supitems: [{
            id: 1,
            name: this.$t('menu.report'),
            sort: 1,
            icon: "el-icon-pie-chart",
            router: "dashboard",
            childMenus: null,
            parentId: null
          },
          {
            id: 2,
            name: this.$t('menu.monitoring'),
            sort: 2,
            icon: "el-icon-monitor",
            router: "monitoring",
            childMenus: null,
            parentId: null
          },
          {
            id: 3,
            name: this.$t('menu.feeder'),
            sort: 3,
            icon: "el-icon-box",
            router: "feeder",
            childMenus: null,
            parentId: null
          },
          {
            id: 4,
            name: this.$t('menu.material'),
            icon: "el-icon-data-analysis",
            sort: 4,
            router: "/material",
            childMenus: [{
                id: 41,
                name: this.$t('menu.storage'),
                sort: 1,
                router: "storage",
                childMenus: null,
                parentId: 4
              },
              {
                id: 43,
                name: this.$t('menu.statistics'),
                sort: 3,
                router: "statistics",
                childMenus: null,
                parentId: 4
              },
              {
                id: 44,
                name: this.$t('menu.update'),
                sort: 4,
                router: "update",
                childMenus: null,
                parentId: 4
              },
              {
                id: 46,
                name: this.$t('menu.freeze'),
                sort: 6,
                router: "freeze",
                childMenus: null,
                parentId: 4
              },
              {
                id: 45,
                name: this.$t('menu.operation'),
                sort: 5,
                router: "operation",
                childMenus: null,
                parentId: 4
              },
            ],
            parentId: null
          },
          {
            id: 10,
            name: this.$t('menu.filemanage'),
            sort: 10,
            router: "filemanage",
            icon: "el-icon-folder-opened",
            childMenus: [{
                id: 101,
                name: this.$t('menu.bomfile'),
                sort: 1,
                router: 'bomfile',
                childMenus: null,
                parentId: 10
              },
              {
                id: 102,
                name: this.$t('menu.feederfile'),
                sort: 2,
                router: 'feederfile',
                childMenus: null,
                parentId: 10
              }
            ],
            parentId: null
          },
          {
            id: 9,
            name: this.$t('menu.ordermanage'),
            sort: 9,
            router: "order",
            icon: "el-icon-s-order",
            childMenus: [{
              id: 91,
              name: this.$t('menu.order'),
              sort: 1,
              router: 'order',
              childMenus: null,
              parentId: 9
            }],
            parentId: null
          },
          {
            id: 5,
            name: this.$t('menu.usermanagement'),
            sort: 1,
            icon: "el-icon-user",
            router: "usermanagement",
            childMenus: [{
                id: 51,
                name: this.$t('menu.user'),
                sort: 1,
                router: "user",
                childMenus: null,
                parentId: 4
              },
              {
                id: 52,
                name: this.$t('menu.usertrace'),
                sort: 2,
                router: "trace",
                childMenus: null,
                parentId: 4
              }
            ],
            parentId: null
          },
          {
            id: 6,
            name: this.$t('menu.shelf'),
            sort: 1,
            router: "shelfconfig",
            icon: "el-icon-receiving",
            childMenus: [{
                id: 61,
                name: this.$t('menu.shelfconfig'),
                sort: 1,
                router: 'materialshelf',
                childMenus: null,
                parentId: 6
              },
              {
                id: 62,
                name: this.$t('menu.warehouseconfig'),
                sort: 1,
                router: 'warehouseconfig',
                childMenus: null,
                parentId: 6
              }
            ],
            parentId: null
          },
          {
            id: 7,
            name: this.$t('menu.serverconfig'),
            sort: 7,
            router: "serverconfig",
            icon: "el-icon-cloudy",
            childMenus: null,
            parentId: null
          },
          {
            id: 8,
            name: this.$t('menu.apirecord'),
            sort: 8,
            router: "apirecord",
            icon: "el-icon-edit-outline",
            childMenus: null,
            parentId: null
          },


        ],
        noritems: [{
            id: 1,
            name: this.$t('menu.report'),
            sort: 1,
            icon: "el-icon-pie-chart",
            router: "dashboard",
            childMenus: null,
            parentId: null
          },
          {
            id: 2,
            name: this.$t('menu.monitoring'),
            sort: 2,
            icon: "el-icon-monitor",
            router: "monitoring",
            childMenus: null,
            parentId: null
          },
          {
            id: 3,
            name: this.$t('menu.feeder'),
            sort: 3,
            icon: "el-icon-box",
            router: "feeder",
            childMenus: null,
            parentId: null
          },
          {
            id: 4,
            name: this.$t('menu.material'),
            icon: "el-icon-data-analysis",
            sort: 4,
            router: "/material",
            childMenus: [{
                id: 41,
                name: this.$t('menu.storage'),
                sort: 1,
                router: "storage",
                childMenus: null,
                parentId: 4
              },
              {
                id: 43,
                name: this.$t('menu.statistics'),
                sort: 3,
                router: "statistics",
                childMenus: null,
                parentId: 4
              },
              {
                id: 44,
                name: this.$t('menu.update'),
                sort: 4,
                router: "update",
                childMenus: null,
                parentId: 4
              },
              {
                id: 46,
                name: this.$t('menu.freeze'),
                sort: 6,
                router: "freeze",
                childMenus: null,
                parentId: 4
              },
              {
                id: 45,
                name: this.$t('menu.operation'),
                sort: 5,
                router: "operation",
                childMenus: null,
                parentId: 4
              },
            ],
            parentId: null
          },
          {
            id: 10,
            name: this.$t('menu.filemanage'),
            sort: 10,
            router: "filemanage",
            icon: "el-icon-folder-opened",
            childMenus: [{
                id: 101,
                name: this.$t('menu.bomfile'),
                sort: 1,
                router: 'bomfile',
                childMenus: null,
                parentId: 10
              },
              {
                id: 102,
                name: this.$t('menu.feederfile'),
                sort: 2,
                router: 'feederfile',
                childMenus: null,
                parentId: 10
              }
            ],
            parentId: null
          },
          {
            id: 9,
            name: this.$t('menu.ordermanage'),
            sort: 9,
            router: "order",
            icon: "el-icon-s-order",
            childMenus: [{
              id: 91,
              name: this.$t('menu.order'),
              sort: 1,
              router: 'order',
              childMenus: null,
              parentId: 9
            }],
            parentId: null
          },
          {
            id: 6,
            name: this.$t('menu.shelf'),
            sort: 1,
            router: "shelfconfig",
            icon: "el-icon-receiving",
            childMenus: [{
              id: 61,
              name: this.$t('menu.shelfconfig'),
              sort: 1,
              router: 'materialshelf',
              childMenus: null,
              parentId: 6
            }, ],
            parentId: null
          },
          {
            id: 8,
            name: this.$t('menu.apirecord'),
            sort: 8,
            router: "apirecord",
            icon: "el-icon-edit-outline",
            childMenus: null,
            parentId: null
          }
        ]

      }
    },
    created() {
      if (this.surpermissionFlag) {
        this.items = this.supitems
      } else {
        this.items = this.noritems
      }
    }
  }
</script>
<style>
  .sidebarshow {
    width: 200px;
  }

  .el-menu-vertical-demo .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  .sidebarhidden {
    width: 64px;
  }

  .sidebar {
    display: block;
    position: absolute;
    /*width: 200px;*/
    /* height: 100%; */
    left: 0;
    top: 42px;
    bottom: 0;
    box-sizing: border-box;
  }

  .sidebar>ul {
    height: 100%;
  }

  .el-menu {
    text-align: left;
  }

  .el-menu-item {
    font-size: 16px !important;
  }

  .el-scrollbar__wrap {
    overflow-x: hidden;
  }

  .sidebar .el-submenu__title {
    font-size: 16px !important;
  }

  .el-menu-vertical-demo {
    height: 100%;
  }
</style>



