<template>
  <div class="header">
    <div class="logo_content">
      <span class="span_icon" @click="showMenu">
        <i class="el-icon-menu icon"></i>
      </span>
      <img src="../../assets/images/swissmic-logo-white.png" alt="">
      <span class="logo_title">Material.S</span>
    </div>
    <div class="user_content">
      <span>{{$t('common.welcome')}}</span>
      <div class="userset">
        <el-dropdown :hide-on-click="true" @command="handleCommand">
          <span class="el-dropdown-link username">
            {{username}}
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="changePsd">修改密码</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <span class="logout_span" @click="loginOut">[ {{$t('common.safeLogout')}} ]</span>
    </div>
    <v-changepsd ref="psd"></v-changepsd>
  </div>
</template>
<script>
  import {
    mapMutations
  } from 'vuex'
  import VChangepsd from './changepsd'
  export default {
    components: {
      VChangepsd
    },
    data() {
      return {
        username: localStorage.getItem('user_name') || '',
        arrowState: false,
        changepassword: false,
      }
    },
    methods: {
      ...mapMutations(['changeCollapse']),
      ...mapMutations(['changeLogin']),
      showMenu() {
        let _this = this;
        this.changeCollapse({
          isCollapse: _this.$store.state.isCollapse
        })
      },
      loginOut() {
        localStorage.removeItem('user_name');
        localStorage.removeItem('user_group_power');
        localStorage.removeItem('user_id');
        this.$router.push('/login')
        setTimeout(() => {
          location.reload() // 强制刷新
        }, 500)
      },
      handleCommand(command) {
        if (command == 'changePsd') {
          this.$refs.psd.open()
        }
      },

    }
  }
</script>
<style scoped>
  .header {
    /* position: relative;*/
    box-sizing: border-box;
    width: 100%;
    height: 42px;
    font-size: 18px;
    line-height: 42px;
    color: #fff;
    background: rgb(43, 45, 52);
  }

  .logo_content {
    float: left;
    margin: 4px 0 0 20px;
    height: 26px;
    line-height: 26px;
  }

  .span_icon {
    font-size: 26px;
    display: inline-block;
    padding-top: 2PX;
    cursor: pointer;
  }

  .span_icon:hover {
    color: #ffd04b;
  }

  .logo_title {
    font-size: 16px;
  }

  .logo_content img {
    width: 90px;
    height: 26px;
  }

  .user_content {
    float: right;
    margin-right: 30px;
    font-size: 13px;
    display: flex;
  }

  .logout_span {
    margin-left: 20px;
    cursor: pointer;
  }

  .logout_span:hover {
    color: #ffd04b;
  }

  .username {
    color: #fff;
    margin-left: 10px;
    cursor: pointer;
  }

  .el-dropdown {
    line-height: 30px;
  }

  .user-info .el-dropdown-link {
    position: relative;
    display: inline-block;
    top: 0px;
    cursor: pointer;
    vertical-align: middle;
    color: red;
  }

  .el-dropdown-menu__item {
    text-align: center;
    height: 30px;
    line-height: 30px;
    margin: -8px 0;
    padding: 0 6px;
  }
</style>



