<template>
  <div>
    <el-dialog :title="$t('user.changepsd')" :visible.sync="psdVisible" @close="reset('psdform')" width="446px">
      <div class="small_form">
        <el-form ref="psdform" :model="psdform" :rules="rules" label-width="100px">
          <el-form-item :label="$t('user.oldpsaword')" prop="old_passwd">
            <el-input v-model="psdform.old_passwd" show-password></el-input>
          </el-form-item>
          <el-form-item :label="$t('user.newpsaword')" prop="new_passwd">
            <el-input v-model="psdform.new_passwd" show-password></el-input>
          </el-form-item>
          <!-- 	<el-form-item :label="$t('user.repassword')" prop="renew_passwd">
			    <el-input v-model="psdform.renew_passwd" show-password></el-input>
			  </el-form-item> -->
          <el-form-item>
            <el-button @click="userCancel('psdform')">{{$t('common.cancel')}}</el-button>
            <el-button type="primary" @click="psdSave('psdorm')">{{$t('common.save')}}</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {
    changeUserPasswd
  } from '@/request/api/user'; // 导入api接口'
  export default {
    data() {
      return {
        psdVisible: false,
        psdform: {
          old_passwd: '',
          new_passwd: '',
          user_id: localStorage.getItem('user_id'),
        },
        rules: {
          old_passwd: [{
            required: true,
            message: this.$t('user.oldpasswordholder'),
            trigger: 'blur'
          }],
          new_passwd: [{
            required: true,
            message: this.$t('user.newpasswordholder'),
            trigger: 'blur'
          }],
        }
      }
    },
    methods: {
      open() {
        this.psdVisible = true
      },
      userCancel(psdform) {
        this.$refs.psdform.resetFields()
        this.psdVisible = false
      },
      psdSave(psdform) {
        this.$refs.psdform.validate((valid) => {
          if (valid) {
            changeUserPasswd(psdform).then(res => {
              if (res.result) {
                this.psdVisible = false
                this.$message({
                  showClose: true,
                  message: 'success',
                  type: 'success'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: res.msg,
                  type: 'error'
                })
              }
            })
          } else {
            return false;
          }
        });
      },
      reset(psdform) {
        this.$refs.psdform.resetFields()
      },
    }
  }
</script>
<style>
  .small_form {
    margin: 0 20px;
  }

  .small_form .el-form-item__label {
    text-align: left;
  }

  .small_form .el-form-item__content {
    width: 260px;
  }

  .small_form .el-input .el-input__inner {
    width: 260px;
  }
</style>



